<template>
  <section class="header-wrapper2">
    <section class="header-wrapper">
      <div class="header-wrapper-right"></div>
      <header class="header">
        <!---->
        <div
          class="left c-gap-left-large c-btn"
          @click="handleHeaderLeftClick">
          <div class="logo">
            logo
          </div>
          <div class="left-right c-gap-left">
            <div class="row1"></div>
            <div class="row2 c-gap-top-small">slogan</div>
          </div>
        </div>
        <!---->
        <nav class="nav">
          <!---->
          <div
            class="nav-item"
            v-for="(item, index) in navList"
            :key="index"
            @mouseenter="handleMouseEnter(index)"
            @mouseleave="handleMouseLeave(index)">
            {{item.text}}
            <transition name="fade">
              <div
                class="mask"
                v-show="item.isShowMask">
                <div
                  v-for="(item2, index2) in item.children"
                  :key="index2"
                  class="mask-item c-gap-inner-left-large transition-animate"
                  @click="handleMaskItemClick(item2)">
                  {{item2.text}}
                </div>
              </div>
            </transition>
          </div>
          <!---->
          <div class="login">
            <i class="iconfont icon-round-user-new"></i>
            <div class="text c-gap-top">
          <span
            class="sign-in transition-animate c-btn"
            @click="handleSignInClick">
            
          </span>/<span
              class="sign-up transition-animate c-btn"
              @click="handleSignUpClick">
            
          </span>
            </div>
          </div>
        </nav>
        <!---->
        <modal
          v-if="isShowModal">
        </modal>
      </header>
    </section>
  </section>
</template>

<script>
  import Modal from '../Modal/Modal.vue';

  export default {
    name: 'Header',
    data() {
      return {
        isShowModal: false,
        navList: [
          {
            text: '',
            isShowMask: false,
            children: [
              {
                text: ''
              },
              {
                text: ''
              }
            ]
          },
          {
            text: '',
            isShowMask: false,
            children: [
              {
                text: '',
                link: '/filter'
              },
              {
                text: '',
                link: '/filter'
              }
            ]
          },
          {
            text: '',
            isShowMask: false,
            children: [
              {
                text: ''
              },
              {
                text: ''
              }
            ]
          },
          {
            text: '',
            isShowMask: false,
            children: [
              {
                text: ''
              },
              {
                text: '',
                link: '/loanapplication'
              },
              {
                text: ''
              }
            ]
          },
          {
            text: '',
            isShowMask: false,
            children: [
              {
                text: ''
              }
            ]
          }
        ]
      }
    },
    methods: {
      handleMouseEnter(index) {
        this.navList[index].isShowMask = true;
      },
      handleMouseLeave(index) {
        this.navList[index].isShowMask = false;
      },
      handleSignInClick() {
        console.log('');
      },
      handleSignUpClick() {
        console.log('');
      },
      handleHeaderLeftClick() {
        this.$router.push('/index');
      },
      handleMaskItemClick(item) {
        this.$router.push(item.link);
      }
    },
    components: {
      Modal
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/app";

  .header-wrapper2 {
    position: fixed;
    width: 100%;
    box-shadow: 0 0 5px 1px $primaryColor;
    background: #fff;
    z-index: 8;
  }

  .header-wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 100%;

    .header-wrapper-right {
      position: absolute;
      top: 0;
      right: 0;
      width: 50%;
      height: 100%;
      background: $primaryColor;
    }
  }

  .header {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: $contentWidth;
    height: $headerHeight;
    z-index: 9;

    .left {
      display: flex;
      align-items: center;

      .logo {
        width: 50px;
        height: 50px;
        line-height: 50px;
        border-radius: 50%;
        background: $primaryColor;
        color: #fff;
        font-size: 12px;
        text-align: center;
      }

      .left-right {

        .row1 {
          font-size: 14px;
        }

        .row2 {
          font-size: 12px;
        }
      }
    }

    .nav {
      display: flex;
      align-items: center;
      height: 100%;
      color: #fff;
      text-align: center;
      font-size: 14px;

      .nav-item {
        transition: $transitionTime;
        position: relative;
        width: 120px;
        height: 100%;
        line-height: $headerHeight;
        background: $primaryColor;
        cursor: pointer;

        &:after {
          content: '';
          position: absolute;
          width: 2px;
          height: 60px;
          top: 10px;
          right: 0;
          background: $secondColor;
        }

        .mask {
          position: absolute;
          width: 120px;
          top: $headerHeight;
          box-sizing: border-box;
          border-left: 1px solid $secondColor;
          z-index: 9;
          background: #fff;
          color: #000;

          .mask-item {
            height: 40px;
            line-height: 40px;
            text-align: left;

            &:hover {
              background: $secondColor;
              color: #fff;
            }
          }
        }
      }

      .login {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 120px;
        height: 100%;
        background: $primaryColor;

        .iconfont {
          font-size: 22px;
        }

        .text {
          font-size: 12px;

          span:hover {
            opacity: .7;
          }
        }
      }
    }
  }
</style>



